                this.bgWhite = false;
                this.borderNone = false
            } else {
                //
                this.bgWhite = true;
                this.borderNone = true
            }
        }

        window.addEventListener('setItemEvent', function(e) {
            that.language = e.newValue
        })
    },
    components: {
        bpModalForm,
        bpButton
    },
